import { SET_HTAG, SET_HOME } from '../actions/z-names'

const structure = {
  htag: 'HOME',
  tohome: true
}

const header = (state = structure, action) => {
  switch (action.type) {
    case SET_HTAG:
      return {
        ...state,
        htag: action.payload
      }
    case SET_HOME:
      return {
        ...state,
        tohome: action.payload
      }
    default:
      return state
  }
}

export default header
