import { SET_INDEX, SET_ABOUT, SET_PORT } from '../actions/z-names'

const structure = {
  indexState: true,
  aboutState: false,
  portState: false,
  index: [
    {
      ind: 1,
      tag: 'ABOUT',
      ref: '/Resume/About',
      cls: 'about'
    },
    {
      ind: 2,
      tag: 'PORTFOLIO',
      ref: '/Resume/Portfolio',
      cls: 'port'
    },
    {
      ind: 3,
      tag: 'CONTACT',
      ref: '#',
      cls: 'contact'
    }
  ],
  about: [
    {
      ind: 1,
      tag: 'HOME',
      ref: '/Resume/Home',
      cls: 'home'
    },
    {
      ind: 2,
      tag: 'PORTFOLIO',
      ref: '/Resume/Portfolio',
      cls: 'port'
    },
    {
      ind: 3,
      tag: 'CONTACT',
      ref: '#',
      cls: 'contact'
    }
  ],
  port: [
    {
      ind: 1,
      tag: 'HOME',
      ref: '/Resume/Home',
      cls: 'home'
    },
    {
      ind: 2,
      tag: 'ABOUT',
      ref: '/Resume/About',
      cls: 'about'
    },
    {
      ind: 3,
      tag: 'CONTACT',
      ref: '#',
      cls: 'contact'
    }
  ]
}

const pageSwitch = (state = structure, action) => {
  switch (action.type) {
    case SET_INDEX:
      return {
        ...state,
        index: structure.index,
        indexState: action.payload
      }
    case SET_ABOUT:
      return {
        ...state,
        about: structure.about,
        aboutState: action.payload
      }
    case SET_PORT:
      return {
        ...state,
        port: structure.port,
        portState: action.payload
      }

    default:
      return state
  }
}

export default pageSwitch
