import { SET_INDEX, SET_ABOUT, SET_PORT } from '../actions/z-names'

export const setIndex = newPage => {
  return {
    type: SET_INDEX,
    payload: newPage
  }
}

export const setAbout = newPage => {
  return {
    type: SET_ABOUT,
    payload: newPage
  }
}

export const setPort = newPage => {
  return {
    type: SET_PORT,
    payload: newPage
  }
}
